.main {
	position: relative;
	background-position: center top;
	background-repeat: no-repeat;
	flex: 1 0 0;
	padding-top: 88px;
	&-bg {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-position: center top;
			object-fit: none;
		}
	}
}
.first {
	position: relative;
	padding-top: 60px;
	&-inner {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&-content {
		position: relative;
		width: 550px;
	}
	&-link {
		position: relative;
		display: inline-flex;
		padding-top: 56px;
		a {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			background: rgba($colorWhite, 0.08);
			flex: 1 0 0;
			min-width: 170px;
			height: 60px;
			//border: 1px solid rgba($colorWhite, 0.03);
			border-radius: 20px;
			padding: 10px 20px;
			&:before {
				content: '';
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background: linear-gradient(to right, rgba($colorWhite, 1) 0%, rgba($colorWhite, 1) 100%) border-box;
				-webkit-mask: linear-gradient($colorWhite 0 0) padding-box, linear-gradient($colorWhite 0 0);
				-webkit-mask-composite: xor;
				mask-composite: exclude;
				border: 1px solid transparent;
				border-radius: 20px;
				opacity: 0.03;
			}
			&:first-child {
				background: linear-gradient(to right, rgba($colorWhite, 0) 0%, rgba($colorWhite, 0.08) 100%);
				&:before {
					background: linear-gradient(to right, rgba($colorWhite, 0) 0%, rgba($colorWhite, 1) 100%) border-box;
				}
			}
			&:last-child {
				background: linear-gradient(to right, rgba($colorWhite, 0.08) 0%, rgba($colorWhite, 0) 100%);
				&:before {
					background: linear-gradient(to right, rgba($colorWhite, 1) 0%, rgba($colorWhite, 0) 100%) border-box;
				}
			}
			&:not(:last-child) {
				margin-right: 4px;
			}
			img {
				max-height: 28px;
			}
		}
	}
	&-card {
		position: relative;
		width: 362px;
		padding-top: 24px;
		//overflow: hidden;
		border-radius: 40px;
		z-index: 0;
		&__tape {
			position: absolute;
			background: $colorBg;
			top: 0;
			left: 0;
			width: 69%;
			height: 80px;
			border-radius: 40px;
			&:before {
				content: '';
				position: absolute;
				top: -36px;
				right: -65px;
				width: 85px;
				height: 60px;
				border-radius: 50%;
				box-shadow: -35px 50px 0px 5px $colorBg;
			}
		}
		&__box {
			position: relative;
			background: $colorBg;
			color: $colorText;
			border-radius: 24px 40px 40px 40px;
			padding: 10px;
			&:before {
				content: '';
				position: absolute;
				background: linear-gradient(to bottom, rgba($colorCian, 1) 0%, rgba($colorCian, 0.04) 100%);
				top: 70px;
				left: 54px;
				width: calc(100% - 32px);
				height: calc(100% - 32px);
				border-radius: 40px;
				transform: rotate(11deg);
				opacity: 0.24;
				z-index: -1;
			}
			&-head {
				position: relative;
				display: flex;
				align-items: center;
				height: 134px;
				margin-top: -14px;
				&:before {
					content: '';
					position: absolute;
					background: linear-gradient(to bottom, rgba($colorLightText, 0.1) 0%, rgba($colorLightText, 1) 50%, rgba($colorLightText, 0.1) 100%);
					top: 0;
					left: 50%;
					height: 100%;
					width: 2px;
					opacity: 0.06;
				}
				&:not(:last-child) {
					margin-bottom: 10px;
				}
			}
			&-info {
				position: relative;
				width: 50%;
				padding: 0 12px 0 22px;
				&-title {
					position: relative;
					font-size: 11px;
					line-height: 1.273;
					text-transform: uppercase;
					opacity: 0.5;
					&:not(:last-child) {
						margin-bottom: 4px;
					}
				}
				&-count {
					position: relative;
					font-size: 64px;
					font-weight: 300;
					line-height: 1;
					letter-spacing: -0.03em;
				}
			}
			&-logo {
				position: relative;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				width: 50%;
				padding-top: 20px;
				&-img {
					position: relative;
					width: 67px;
					&:not(:last-child) {
						margin-bottom: 10px;
					}
				}
				&-title {
					position: relative;
					font-size: 10px;
					line-height: 1.4;
					opacity: 0.3;
				}
			}
			&-chart {
				position: relative;
				background: $colorWhite;
				border-radius: 32px;
				padding: 28px 16px;
				&:not(:last-child) {
					margin-bottom: 10px;
				}
			}
			&-button {
				position: relative;
				.btn {
					width: 100%;
				}
			}
		}
	}
}
.partners {
	position: relative;
	&-inner {
		position: relative;
	}
	&-group {
		position: relative;
		padding-top: 52px;
	}
	&-tabs {
		position: relative;
		display: flex;
		overflow: hidden;
		&__box {
			position: relative;
			height: 52px;
			min-width: 185px;
			border-radius: 40px;
			cursor: pointer;
			&:before {
				content: '';
				position: absolute;
				background: $colorDarkBg;
				top: 0;
				left: 0;
				width: 100%;
				height: 100px;
				border-radius: 40px;
				pointer-events: none;
			}
			&:hover {
				.partners-tabs__box {
					&-arrow {
						&:before {
							transform: translate(-50%, -50%) scale(1);
							transition: transform 0.5s cubic-bezier(0.1, 0, 0.3, 1);
						}
					}
				}
			}
			&.active {
				z-index: 1;
				&:before {
					background: $colorWhite;
				}
				.partners-tabs {
					&__box {
						&-tape {
							&:before {
								box-shadow: -8px 58px 0px 5px $colorWhite;
							}
							&:after {
								box-shadow: -8px 58px 0px 5px $colorWhite;
							}
						}
						&-img {
							img {
								filter: brightness(1);
								opacity: 1;
							}
						}
					}
				}
			}
			&:first-child {
				.partners-tabs {
					&__box {
						&-tape {
							&:before {
								content: none;
							}
						}
					}
				}
			}
 			&-tape {
				&:before {
					content: '';
					position: absolute;
					top: -22px;
					left: -70px;
					width: 74px;
					height: 74px;
					border-radius: 50%;
					box-shadow: -8px 58px 0px 5px $colorDarkBg;
					transform: rotate(-40deg);
					pointer-events: none;
				}
				&:after {
					content: '';
					position: absolute;
					top: -22px;
					right: -70px;
					width: 74px;
					height: 74px;
					border-radius: 50%;
					box-shadow: -8px 58px 0px 5px $colorDarkBg;
					transform: rotate(28deg);
					pointer-events: none;
				}
			}
			&-img {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				text-align: center;
				height: 52px;
				img {
					max-height: 24px;
					filter: brightness(0);
					opacity: 0.3;
				}
			}
			&-arrow {
				position: absolute;
				display: none;
				align-items: center;
				justify-content: center;
				background: $colorText;
				top: 50%;
				right: 16px;
				width: 40px;
				height: 40px;
				color: $colorWhite;
				font-size: 20px;
				border-radius: 50%;
				transform: translateY(-50%);
				&:before {
					content: '';
					position: absolute;
					background: $colorCian;
					top: 50%;
					left: 50%;
					width: calc(100% + 1px);
					height: calc(100% + 1px);
					border-radius: 50%;
					transform: translate(-50%, -50%) scale(0);
				}
			}
		}
	}
	&-content {
		position: relative;
		background: $colorWhite;
		border-radius: 0 40px 40px 40px;
		z-index: 1;
	}
	&-box {
		position: relative;
		flex-direction: row-reverse;
		justify-content: space-between;
		background: $colorWhite;
		padding: 24px 24px 36px 36px;
		border-radius: 0 40px 40px 40px;
		overflow: hidden;
		&__map {
			position: absolute;
			top: 16px;
			left: 0;
			width: 1235px;
			margin-left: -95px;
			transform: translateX(-50%);
		}
		&__head {
			position: relative;
			align-self: flex-end;
			width: 380px;
		}
		&__info {
			position: relative;
			font-size: 40px;
			line-height: 1;
			letter-spacing: -0.03em;
			&:not(:last-child) {
				margin-bottom: 48px;
			}
			b, strong {
				color: $colorCian;
				font-weight: 400;
			}
		}
		&__tip {
			position: relative;
			color: rgba($colorDarkText, 0.5);
			font-size: 11px;
			line-height: 1.455;
			text-transform: uppercase;
			b, strong {
				color: $colorDarkText;
				font-weight: 600;
			}
		}
		&__body {
			position: relative;
			width: 530px;
		}
		&__desc {
			position: relative;
			background: rgba($colorBg, 0.8);
			height: 316px;
			border-radius: 32px;
			padding: 36px 64px 36px 36px;
			overflow: hidden;
			&:not(:last-child) {
				margin-bottom: 82px;
			}
		}
		&__title {
			position: relative;
			font-size: 28px;
			line-height: 1.1;
			letter-spacing: -0.03em;
			&:not(:last-child) {
				margin-bottom: 20px;
			}
		}
		&__text {
			position: relative;
			max-width: 364px;
			font-size: 16px;
			line-height: 1.48;
			letter-spacing: -0.01em;
			opacity: 0.7;
		}
		&__progress {
			position: relative;
			padding: 0 10px;
			z-index: 0;
			&:not(:last-child) {
				margin-bottom: 36px;
			}
			&-tip {
				position: absolute;
				background: rgba($colorCian, 0.12);
				bottom: calc(100% + 12px);
				left: 17px;
				width: 52px;
				height: 32px;
				color: $colorCian;
				font-size: 16px;
				font-weight: 600;
				line-height: 1.5;
				letter-spacing: -0.03em;
				text-align: center;
				border-radius: 8px;
				padding: 4px 8px;
				margin-left: 10px;
				transform: translateX(-50%);
				&:before {
					content: '';
					position: absolute;
					top: 100%;
					left: 50%;
					border-style: solid;
					border-width: 5px 4px 0 4px;
					border-color: rgba($colorCian, 0.12) transparent transparent transparent;
					transform: translateX(-50%);
				}
			}
			&-line {
				position: absolute;
				background: linear-gradient(to right, rgba($colorLightText, 0.1) 0%, rgba($colorLightText, 1) 50%, rgba($colorLightText, 0.1) 100%);
				top: 17px;
				left: 0;
				width: 100%;
				height: 1px;
				opacity: 0.06;
			}
			&-bar {
				position: relative;
				display: flex;
				justify-content: space-between;
			}
			&-step {
				position: relative;
				display: flex;
				flex-direction: column;
				align-items: flex-end;
				width: 80px;
				&.active {
					align-items: flex-start;
					.partners-box__progress {
						&-point {
							&:after {
								background: $colorCian;
							}
						}
						&-title {
							border-radius: 0 8px 8px 8px;
						}
					}
				}
				&.current {
					align-items: center;
					.partners-box__progress {
						&-point {
							border-width: 5px;
							&:before {
								content: '';
								position: absolute;
								background: $colorWhite;
								top: 50%;
								left: 50%;
								width: 41px;
								height: 41px;
								border-radius: 50%;
								border: 1px solid $colorLightBg;
								transform: translate(-50%, -50%);
								z-index: -1;
							}
							&:after {
								width: 9px;
								height: 9px;
								border-radius: 3px;
							}
						}
						&-title {
							border-radius: 8px;
						}
					}
				}
			}
			&-point {
				position: relative;
				background: $colorWhite;
				width: 35px;
				height: 35px;
				border-radius: 50%;
				border: 8px solid $colorLightBg;
				&:not(:last-child) {
					margin-bottom: 10px;
				}
				&:after {
					content: '';
					position: absolute;
					background: $colorDarkBorder;
					top: 50%;
					left: 50%;
					width: 7px;
					height: 7px;
					border-radius: 2px;
					transform: translate(-50%, -50%);
				}
			}
			&-title {
				position: relative;
				background: $colorLightBg;
				width: 100%;
				color: rgba($colorLightText, 0.3);
				font-size: 10px;
				line-height: 1.2;
				text-align: center;
				text-transform: uppercase;
				border-radius: 8px 0 8px 8px;
				padding: 5px 8px;
				b, strong {
					color: $colorCian;
					font-weight: 600;
				}
			}
		}
		&__buttons {
			position: relative;
			display: flex;
			padding: 0 10px;
			.btn {
				flex: 1 0 0;
				&:not(:last-child) {
					margin-right: 10px;
				}
			}
		}
	}
}
