.faq {
	position: relative;
	&-inner {
		position: relative;
	}
	&-content {
		position: relative;
		max-width: 940px;
		padding-top: 42px;
		margin: 0 auto;
	}
	&-tabs {
		position: relative;
		text-align: center;
		&:not(:last-child) {
			margin-bottom: 40px;
		}
		ul {
			display: inline-flex;
			align-items: center;
			justify-content: center;
			li {
				position: relative;
				font-size: 11px;
				line-height: 1.273;
				text-transform: uppercase;
