@font-face
	font-family: "svgfont"
	src: url('fonts/svgfont.eot')
	src: url('fonts/svgfont.eot?#iefix') format('eot'), url('fonts/svgfont.woff') format('woff'), url('fonts/svgfont.woff2') format('woff2'), url('fonts/svgfont.ttf') format('truetype')
	font-weight: normal
	font-style: normal

$icon-close: "\EA01"
$icon-i-chevron-left: "\EA02"
$icon-i-chevron-right: "\EA03"
$icon-i-chevron-up: "\EA04"
$icon-i-chevron-down: "\EA05"
$icon-i-plus: "\EA06"
$icon-i-quote: "\EA07"
$icon-i-minus: "\EA08"
$icon-i-check: "\EA09"
$icon-i-arr-down: "\EA0A"


%icon
	font-family: "svgfont"
	font-style: normal
	font-weight: normal
	text-rendering: auto
	speak: none
	line-height: 1
	-webkit-font-smoothing: antialiased
	-moz-osx-font-smoothing: grayscale

.icon, [class^="icon-"], [class*=" icon-"]
	@extend %icon
	display: inline-block

.icon-close:before
	content: $icon-close
.icon-i-chevron-left:before
	content: $icon-i-chevron-left
.icon-i-chevron-right:before
	content: $icon-i-chevron-right
.icon-i-chevron-up:before
	content: $icon-i-chevron-up
.icon-i-chevron-down:before
	content: $icon-i-chevron-down
.icon-i-plus:before
	content: $icon-i-plus
.icon-i-quote:before
	content: $icon-i-quote
.icon-i-minus:before
	content: $icon-i-minus
.icon-i-check:before
	content: $icon-i-check
.icon-i-arr-down:before
	content: $icon-i-arr-down