$colorBlack: #000;
$colorWhite: #FFF;
$colorGray: #666;
$colorLightGrey: #999;
$colorDarkGrey: #333;
$colorGreen: #00CC00;
$colorLightGreen: lighten($colorGreen, 10);
$colorDarkGreen: darken($colorGreen, 10);
$colorRed: #FF4337;
$colorLightRed: lighten($colorRed, 10);
$colorDarkRed: darken($colorRed, 10);

$colorBg: #F0F3F5;
$colorLightBg: #F3F5F7;
$colorLightenBg: #ECEFF1;
$colorDarkBg: #F8F9FB;
$colorDarkenBg: #EFF2F5;
$colorBorder: #E0E3E6;
$colorLightBorder: #E7E7EB;
$colorDarkBorder: #E5E6E9;
$colorText: #282A38;
$colorLightText: #2A2C3A;
$colorLightenText: #3C3E4F;
$colorDarkText: #242631;
$colorDarkenText: #2B2D3A;
$colorCian: #15C87F;
$colorLightCian: #19C07C;
$colorDarkCian: #0EBD76;

html,
body {
	height: 100%;
	scrollbar-color: rgba($colorWhite, 0.5) rgba($colorText, 0.8);
	scrollbar-width: 8px;
}
body {
	background: $colorText;
	color: $colorWhite;
	font-size: 14px;
	font-family: 'Averta CY', sans-serif;
	line-height: 1.25;
}
::-webkit-scrollbar {
	width: 8px;
}
::-webkit-scrollbar-track {
	background-color: rgba($colorText, 0.8);
	border-radius: 4px;
}
::-webkit-scrollbar-thumb {
	background-color: rgba($colorWhite, 0.5);
	border-radius: 4px;
}
* {
	outline: none;
	box-sizing: border-box;
	&:before,
	&:after {
		box-sizing: border-box;
	}
}
h1, h2, h3, h4, h5, h6 {
	font-size: 1em;
	line-height: 1;
	margin: 0;
}
img {
	vertical-align: top;
	max-width: 100%;
}
a {
	text-decoration: none;
	&:hover{
		text-decoration: none;
	}
}
ul,
ol {
	list-style-type: none;
	list-style-position: inside;
	padding: 0;
	margin: 0;
}
p {
	margin: 0;
}
button {
	outline: none;
	&:focus{
		outline: none;
	}
}
[class^='icon-'],
[class*=' icon-'] {
	position: relative;
	vertical-align: middle;
}
.wrapper {
	position: relative;
	display: flex;
	flex-direction: column;
	//width: 100vw;
	//max-height: 100vh;
	min-width: 375px;
	min-height: 100%;
	overflow: hidden;
	&[data-scrollbar] {
		display: flex;
	}
	& > .scroll-content {
		display: flex;
		flex-direction: column;
		flex: 1 0 0;
		max-width: 100vw;
	}
}
.container {
	//max-width: 1136px;
	max-width: 1184px;
	padding: {
		left: 24px;
		right: 24px;
	};
	margin: {
		left: auto;
		right: auto;
	};
}
.section {
	position: relative;
	padding: 108px 0;
	&-group {
		position: relative;
		padding: 108px 0;
		margin: 0 24px;
		overflow: hidden;
		.section {
			padding: 0;
			&:not(:last-child) {
				margin-bottom: 92px;
			}
		}
	}
}
.title {
	position: relative;
	&:not(:last-child) {
		margin-bottom: 28px;
	}
	h1 {
		font-size: 84px;
		font-weight: 400;
		line-height: 0.9;
		letter-spacing: -0.03em;
		b, strong {
			color: $colorCian;
			font-weight: 400;
		}
	}
	h2 {
		font-size: 72px;
		font-weight: 400;
		line-height: 1;
		letter-spacing: -0.03em;
	}
}
.subtitle {
	position: relative;
	color: rgba($colorWhite, 0.7);
	font-size: 18px;
	line-height: 1.5;
	letter-spacing: -0.01em;
	//opacity: 0.7;
	&.text-center {
		margin: 0 auto;
	}
}
.text {
	&-left {
		text-align: left !important;
	}
	&-center {
		text-align: center !important;
	}
	&-right {
		text-align: right !important;
	}
}
.bg {
	&-gray {
		background: $colorBg;
		color: $colorText;
		border-radius: 40px;
		.subtitle {
			color: rgba($colorText, 0.7);
		}
	}
}
.slick {
	&-arrow {
		position: absolute;
		background: rgba($colorWhite, 0.1);
		top: 50%;
		width: 56px;
		height: 56px;
		font-size: 0;
		border: 0;
		border-radius: 50%;
		padding: 0;
		transform: translateY(-50%);
		transition: background 0.5s;
		cursor: pointer;
		z-index: 10;
		&:before {
			content: '';
			display: flex;
			align-items: center;
			justify-content: center;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			color: $colorWhite;
			font-size: 18px;
			font-family: 'svgfont', sans-serif;
		}
		&:hover {
			background: rgba($colorWhite, 0.18);
		}
	}
	&-prev {
		left: 0;
		&:before {
			content: '\EA02';
		}
	}
	&-next {
		right: 0;
		&:before {
			content: '\EA03';
		}
	}
	&-disabled {
		background: rgba($colorWhite, 0.04);
		pointer-events: none;
		&:before {
			opacity: 0.3;
		}
	}
	&-dots {
		position: absolute;
		display: flex;
		align-items: center;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		li {
			font-size: 0;
			margin: 0 4px;
			&.slick-active {
				button {
					background: $colorLightCian;
					transform: scale(1.3325);
					&:before {
						content: '';
						position: absolute;
						top: -2px;
						right: -2px;
						bottom: -2px;
						left: -2px;
						border-radius: 50%;
						border: 1px solid $colorLightCian;
						opacity: 0.3;
					}
				}
			}
			button {
				background: $colorLightBorder;
				width: 6px;
				height: 6px;
				border: 0;
				border-radius: 50%;
				padding: 0;
			}
		}
	}
}
