.footer {
	position: relative;
	padding: 32px 0 86px;
	&-top {
		position: relative;
		&:not(:last-child) {
			margin-bottom: 54px;
		}
		.footer {
			&-inner {
				align-items: flex-start;
			}
		}
	}
	&-bottom {
		position: relative;
	}
	&-inner {
		position: relative;
		display: flex;
		align-items: flex-end;
	}
	&-menu {
		position: relative;
		width: 548px;
		padding-top: 8px;
		&:not(:first-child) {
			margin-left: 138px;
		}
		ul {
			column-count: 2;
			column-gap: 24px;
			page-break-inside: avoid;
			li {
				//position: relative;
				display: inline-block;
				vertical-align: top;
				width: 100%;
				margin-bottom: 14px;
				&:hover {
					ul {
						transition: opacity 0.5s;
						visibility: visible;
						opacity: 1;
					}
					& > .icon {
						transform: rotateX(180deg);
					}
				}
				a {
					color: $colorWhite;
					font-size: 15px;
					line-height: 1.33334;
					transition: color 0.5s;
					&:hover {
						color: $colorCian;
					}
				}
				.icon {
					font-size: 14px;
					margin-left: 4px;
					cursor: pointer;
					opacity: 0.4;
				}
				ul {
					position: absolute;
					background: $colorBg;
					display: block;
					//top: 100%;
					left: 0;
					//min-width: 100%;
					width: 160px;
					column-count: 1;
					border-radius: 8px;
					visibility: hidden;
					opacity: 0;
					z-index: 1;
					li {
						padding: 0;
						margin-bottom: 0;
						&:not(:last-child) {
							border-bottom: 1px solid $colorBorder;
						}
						a {
							display: block;
							color: $colorText;
							padding: 8px 14px;
							transition: opacity 0.5s;
							opacity: 0.5;
							&:hover {
								color: $colorText;
								opacity: 1;
							}
						}
					}
				}
			}
		}
	}
	&-award {
		position: relative;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-column-gap: 42px;
		grid-row-gap: 48px;
		width: 262px;
		line-height: 0;
		padding-top: 12px;
		margin-left: auto;
		&__box {
			position: relative;
			a {
				position: relative;
				display: inline-block;
				vertical-align: top;
				transition: opacity 0.5s;
				opacity: 0.2;
				&:hover {
					opacity: 0.8;
				}
			}
		}
	}
}
.copyright {
	position: relative;
	font-size: 11px;
	line-height: 1.636;
	letter-spacing: 0.02em;
	text-transform: uppercase;
	opacity: 0.6;
}
.develop {
	position: relative;
	font-size: 11px;
	line-height: 1.636;
	letter-spacing: 0.02em;
	text-transform: uppercase;
	margin-left: auto;
	opacity: 0.6;
	a {
		color: $colorWhite;
		text-decoration: underline;
	}
}
