.btn {
	position: relative;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	background: $colorCian;
	height: 56px;
	color: $colorWhite;
	font-size: 16px;
	font-weight: 400;
	font-family: 'Averta CY', sans-serif;
	line-height: 1.25;
	letter-spacing: -0.01em;
	text-align: center;
	text-decoration: none;
	border: 0;
	border-radius: 40px;
	box-shadow: none;
	padding: 8px 24px;
	will-change: transform;
	transition: color 0.5s;
	outline: none;
	cursor: pointer;
	overflow: hidden;
	&:hover,
	&:active {
		color: $colorWhite;
		text-decoration: none;
		outline: none;
	}
	&:focus {
		box-shadow: none;
		outline: none;
	}
	&-green {
		background: $colorCian;
		color: $colorWhite;
		&:before {
			content: '';
			position: absolute;
			background: $colorDarkCian;
			top: 50%;
			left: 50%;
			width: 100%;
			border-radius: 50%;
			padding-top: 100%;
			transition: none;
			transform: translate(-50%, -50%) scale(0);
		}
		&:hover,
		&:active {
			color: $colorWhite;
			&:before {
				transition: transform 0.5s cubic-bezier(0.1, 0, 0.3, 1);
				transform: translate(-50%, -50%) scale(1);
			}
		}
	}
	&-white {
		background: $colorWhite;
		color: $colorText;
		&:before {
			content: '';
			position: absolute;
			background: $colorDarkCian;
			top: 50%;
			left: 50%;
			width: 100%;
			border-radius: 50%;
			padding-top: 100%;
			transition: none;
			transform: translate(-50%, -50%) scale(0);
		}
		&:hover,
		&:active {
			color: $colorWhite;
			&:before {
				transition: transform 0.5s cubic-bezier(0.1, 0, 0.3, 1);
				transform: translate(-50%, -50%) scale(1.01);
			}
		}
	}
	&-gray {
		background: rgba($colorWhite, 0.1);
		color: $colorWhite;
		&:before {
			content: '';
			position: absolute;
			background: $colorCian;
			top: 50%;
			left: 50%;
			width: 100%;
			border-radius: 50%;
			padding-top: 100%;
			transition: none;
			transform: translate(-50%, -50%) scale(0);
		}
		&:hover,
		&:active {
			color: $colorWhite;
			&:before {
				transition: transform 0.5s cubic-bezier(0.1, 0, 0.3, 1);
				transform: translate(-50%, -50%) scale(1);
			}
		}
	}
	&-border {
		background: transparent;
		color: $colorLightText;
		border: 1px solid rgba($colorLightText, 0.1);
		&:before,
		&:after {
			content: '';
			position: absolute;
			background: rgba($colorLightText, 0.6);
			width: 0;
			height: 1px;
		}
		&:before {
			right: 26px;
			bottom: 0;
		}
		&:after {
			left: 26px;
			top: 0;
		}
		&:hover {
			color: $colorLightText;
			transition: background 0.3s;
			&:before,
			&:after {
				width: calc(100% - 52px);
				transition: width 0.15s linear;
			}
			&:after {
				transition-delay: 0.3s;
			}
			.semicircle {
				&-left,
				&-right {
					animation: arcLine 0.15s linear forwards;
				}
				&-left {
					animation-delay: 0.15s;
				}
				&-right {
					animation-delay: 0.45s;
				}
			}
		}
		.semicircle {
			&-left,
			&-right {
				position: absolute;
				top: 14px;
				width: 56px;
				stroke: rgba($colorLightText, 0.6);
				stroke-width: 1px;
				stroke-dasharray: 81;
				stroke-dashoffset: 157;
			}
			&-left {
				left: -15px;
				transform: rotate(-90deg);
			}
			&-right {
				right: -15px;
				transform: rotate(90deg);
			}
		}
	}
	&-border-white {
		background: transparent;
		color: $colorWhite;
		border: 1px solid rgba($colorWhite, 0.4);
		&:before,
		&:after {
			content: '';
			position: absolute;
			background: rgba($colorWhite, 1);
			width: 0;
			height: 1px;
		}
		&:before {
			right: 26px;
			bottom: 0;
		}
		&:after {
			left: 26px;
			top: 0;
		}
		&:hover {
			color: $colorWhite;
			transition: background 0.3s;
			&:before,
			&:after {
				width: calc(100% - 52px);
				transition: width 0.15s linear;
			}
			&:after {
				transition-delay: 0.3s;
			}
			.semicircle {
				&-left,
				&-right {
					animation: arcLine 0.15s linear forwards;
				}
				&-left {
					animation-delay: 0.15s;
				}
				&-right {
					animation-delay: 0.45s;
				}
			}
		}
		.semicircle {
			&-left,
			&-right {
				position: absolute;
				top: 14px;
				width: 56px;
				stroke: rgba($colorWhite, 1);
				stroke-width: 1px;
				stroke-dasharray: 81;
				stroke-dashoffset: 157;
			}
			&-left {
				left: -15px;
				transform: rotate(-90deg);
			}
			&-right {
				right: -15px;
				transform: rotate(90deg);
			}
		}
	}
	&-sm {
		height: 38px;
		font-size: 14px;
		border-radius: 20px;
		padding: 4px 12px;
	}
	&.disabled,
	&[disabled] {
		pointer-events: none;
		opacity: 0.5;
		cursor: auto;
	}
	span {
		position: relative;
	}
}
