@media screen and (max-width: 767px) {
	.section {
		padding: 44px 0;
		&-group {
			padding: 44px 0;
			margin: 0;
			.section {
				&:not(:last-child) {
					margin-bottom: 52px;
				}
			}
		}
	}
	.title {
		&:not(:last-child) {
			margin-bottom: 24px;
		}
		h1 {
			font-size: 40px;
			line-height: 1;
		}
		h2 {
			font-size: 40px;
		}
	}
	.subtitle {
		font-size: 16px;
	}
	.form {
		&-title {
			font-size: 24px;
		}
	}
	input {
		height: 48px;
		font-size: 10px;
	}
	textarea {
		height: 120px;
		font-size: 10px;
	}
	.header {
		&-button {
			.btn {
				width: 100%;
			}
		}
	}
	.logo {
		img {
			max-height: 37px;
		}
	}
	.first {
		padding-top: 14px;
		&-content {
			width: 100%;
			&:not(:last-child) {
				margin-bottom: 38px;
			}
		}
		&-link {
			padding-top: 0;
			a {
				min-width: 107px;
				padding: 8px;
				img {
					max-height: 20px;
				}
			}
		}
		&-card {
			width: 326px;
			&__box {
				&:before {
					content: none;
				}
				&-head {
					height: 100px;
					margin-top: -20px;
				}
				&-info {
					padding-left: 16px;
					&-title {
						font-size: 9px;
					}
					&-count {
						font-size: 44px;
					}
				}
				&-logo {
					&-img {
						width: 48px;
						&:not(:last-child) {
							margin-bottom: 5px;
						}
					}
				}
				&-chart {
					padding: 16px;
				}
			}
		}
	}
	.partners {
		&-group {
			padding-top: 8px;
		}
		&-tabs {
			display: block;
			&__box {
				background: $colorWhite;
				height: 72px;
				min-width: 0;
				border-radius: 40px;
				padding: 8px 24px;
				&:before {
					content: none;
				}
				&.active {
					border-radius: 40px 40px 0 0;
					.partners-tabs__box {
						&-arrow {
							.icon {
								&:before {
									content: '\EA08';
								}
							}
						}
					}
				}
				&:not(:first-child) {
					margin-top: 8px;
				}
				&-tape {
					display: none;
				}
				&-img {
					justify-content: flex-start;
				}
				&-arrow {
					display: flex;
				}
			}
		}
		&-content {
			display: none;
		}
		&-box {
			display: none;
			border-radius: 0 0 40px 40px;
			padding-bottom: 28px;
			z-index: 1;
			&:before {
				content: '';
				position: absolute;
				background: $colorDarkBorder;
				top: 0;
				left: 50%;
				width: calc(100% - 20px);
				height: 1px;
				transform: translateX(-50%);
				opacity: 0.45;
			}
			&__map {
				width: 680px;
				top: 55px;
				margin-left: -90px;
			}
			&__info {
				font-size: 32px;
				&:not(:last-child) {
					margin-bottom: 68px;
				}
			}
			&__tip {
				font-size: 10px;
			}
			&__body {
				&:not(:first-child) {
					margin-bottom: 24px;
				}
			}
			&__desc {
				&:not(:last-child) {
					margin-bottom: 36px;
				}
			}
			&__title {
				font-size: 24px;
				&:not(:first-child) {
					margin-bottom: 16px;
				}
			}
			&__text {
				font-size: 14px;
				line-height: 1.5;
				letter-spacing: -0.02em;
			}
			&__progress {
				padding: 0;
				&:not(:first-child) {
					margin-bottom: 26px;
				}
				&-tip {
					width: 50px;
					height: 30px;
					font-size: 14px;
					padding: 5px 8px;
					margin-left: 0;
				}
				&-step {
					width: 48px;
					&:not(.current) {
						.partners-box__progress {
							&-point {
								transform: scale(0.8);
							}
						}
					}
				}
				&-title {
					font-size: 9px;
					padding: 2px 8px;
					b, strong {
						display: block;
					}
				}
			}
			&__buttons {
				display: block;
				padding: 0;
				margin: 0 -14px;
				.btn {
					width: 100%;
					&:not(:last-child) {
						margin-right: 0;
						margin-bottom: 8px;
					}
				}
			}
		}
	}
